'use client';

import { motion } from 'framer-motion';
import { useEffect, useState } from 'react';

export default function GlobalBackground() {
  const [circles, setCircles] = useState<
    { id: number; style: any; duration: number; delay: number }[]
  >([]);

  useEffect(() => {
    setCircles(
      Array.from({ length: 30 }).map((_, i) => {
        const size = Math.random() * 20 + 30; // 30–50px
        return {
          id: i,
          style: {
            width: `${size}px`,
            height: `${size}px`,
            top: `${Math.random() * 100}%`,
            left: '-150px',
          },
          duration: Math.random() * 10 + 10, // 10–20s
          delay: Math.random() * 5,
        };
      })
    );
  }, []);

  if (circles.length === 0) return null; // Prevents mismatch on first render

  return (
    <div className="fixed inset-0 -z-10 overflow-hidden pointer-events-none bg-white">
      {circles.map(({ id, style, duration, delay }) => (
        <motion.div
          key={id}
          className="absolute rounded-full bg-white border-1 border-black opacity-80 shadow-[0_0_60px_rgba(255,255,255,0.6)]"
          style={style}
          animate={{
            x: '130vw',
            opacity: [0, 0.8, 0.8, 0],
          }}
          transition={{
            repeat: Infinity,
            duration,
            ease: 'linear',
            delay,
          }}
        />
      ))}
    </div>
  );
}